import { registerApplication, start } from "single-spa";
import { authService } from "@talygen/authservice";
import initAuthService from "./initAuthService";
import * as prefix from './activity-functions';
if(!localStorage.getItem("oidc.user:https://applogin.talygen.com:talygenspa")){
  if (
    location.pathname == "/DownloadPublicFile" ||
    location.pathname == "DownloadPublicFolder"
  ) {
    registerApplication({
      name: "@talygen/docstorage",
      app: () => System.import("https://talygenv.github.io/Single_SPA_DOCSTORAGE_APP/dist/app.js"),
      activeWhen: (location) =>
        prefix.prefix(location, ["DownloadPublicFile", "DownloadPublicFolder"]),
      customProps: { userProfile: userProfileInfo },
    });
  } else {
initAuthService();
var userProfileInfo = null;
authService.getUser().then(function (response) {
  userProfileInfo = response;
  registerApplication({
    name: "@talygen/navbar",
    app: () => System.import("@talygen/navbar"),
    activeWhen: "/",
    customProps: { userProfile: userProfileInfo },
  });
  registerApplication({
    name: "@talygen/crm",
    app: () => System.import("https://talygenv.github.io/Single_SPA_CRM_APP/dist/app.js"),
    activeWhen: (location) =>prefix.prefix(location,['CRM','Brand','CommunicationModes','CRMReport','User','logout']),
    customProps: { userProfile: userProfileInfo },
  });
  // registerApplication({
  //   name: "@talygen/expenses",
  //   app: () => System.import("http://localhost:9004/app.js"),
  //   activeWhen: (location) =>prefix.prefix(location,['Expenses','ExpenseApproval','ExpenseReport']),
  //   customProps: { userProfile: userProfileInfo },
  // });
  registerApplication({
    name: "@talygen/docstorage",
    app: () => System.import("https://talygenv.github.io/Single_SPA_DOCSTORAGE_APP/dist/app.js"),
    activeWhen: (location) => prefix.prefix(location,['File','ViewFileDetails','RecyleBin','Storage','MyDashboard','ViewFolderDetails','AdvancedSetup','UserStorageReport','UserStorage','ManageTags','DownloadPublicFile','DownloadPublicFolder']), 
    customProps: { userProfile: userProfileInfo },
  });
});
  }
}else{
  var userProfileInfo =JSON.parse(localStorage.getItem("oidc.user:https://applogin.talygen.com:talygenspa"));
  registerApplication({
    name: "@talygen/navbar",
    app: () => System.import("@talygen/navbar"),
    activeWhen: "/",
    customProps: { userProfile: userProfileInfo },
  });
  registerApplication({
    name: "@talygen/crm",
    app: () => System.import("https://talygenv.github.io/Single_SPA_CRM_APP/dist/app.js"),
    activeWhen: (location) =>prefix.prefix(location,['CRM','Brand','CommunicationModes','CRMReport','User','logout']),
    customProps: { userProfile: userProfileInfo },
  });
  // registerApplication({
  //   name: "@talygen/expenses",
  //   app: () => System.import("http://localhost:9004/app.js"),
  //   activeWhen: (location) =>prefix.prefix(location,['Expenses','ExpenseApproval','ExpenseReport']),
  //   customProps: { userProfile: userProfileInfo },
  // });
  registerApplication({
    name: "@talygen/docstorage",
    app: () => System.import("https://talygenv.github.io/Single_SPA_DOCSTORAGE_APP/dist/app.js"),
    activeWhen: (location) => prefix.prefix(location,['File','ViewFileDetails','RecyleBin','Storage','MyDashboard','ViewFolderDetails','AdvancedSetup','UserStorageReport','UserStorage','ManageTags','DownloadPublicFile','DownloadPublicFolder']), 
    customProps: { userProfile: userProfileInfo },
  });
}
start();